import React, {useEffect}from "react";
import BoostrapNavbar from "./Component/Navbar";
import "./App.css"

import { fetchingCategories } from "./Store/CategoriesSlice";
import { useDispatch } from "react-redux";
import { fetchingProducts } from "./Store/ProductsSlices";


function App() {
const dispatch = useDispatch()


useEffect(() => {

  dispatch(fetchingCategories())
  dispatch(fetchingProducts())


})


  return (
    <div className="App">

      <BoostrapNavbar/>
    </div>
  );
}

export default App