import React, {useState} from 'react'
import PropTypes from 'prop-types'
import "./ProductDetails.css"
import { useSelector, useDispatch } from 'react-redux/es/exports'
import { Card } from 'react-bootstrap';
import Quantity from './Quantity';
import { useNavigate } from "react-router-dom";
import Products from './Products';
import {add} from "../Store/CartSlices"
import { CgStories } from 'react-icons/cg';
import CustomizedButton from './CustomizedButton';





const ProductDetails = props => {
  const store = useSelector(state => state.cart)
  const product = store.currentProductdetails;



  const location = useNavigate();
  const dispatch = useDispatch();
  const [currentPhoto, setCurrentPhoto] = useState(store.currentProductdetails.images[0]);


  const findProduct = store.allProducts.find((item) => item.productID == product.id)
//   const getproductQuantity = findProduct == undefined ? 0 : getproductQuantity.quantity


// console.log(getproductQuantity );
const handelAddProductToCart = (product)=>{
  dispatch(add(product))
}

  return (
    <div className="container mt-5 mb-5">
      <div className="row d-flex justify-content-center">
        <div className="col-md-10">
          <div className="card">
            <div className="row">
              <div className="col-md-6">
                <div className="images p-3">
                  <div className="text-center p-4"><Card.Img variant="top" src={currentPhoto} width="250" />
                  </div>
                  <div className="thumbnail text-center"> 
                  {
     
                    product.images.map((img, index) => {
                      return (
                        
                        <a key={index} onClick={() => setCurrentPhoto(img)}><Card.Img variant="top" src={img} style={{width:"5vw"}} /></a>
                      )

                    })
                  }
                  </div>
                </div>
              </div>
              <div className="col-md-6">
                <div className="product p-4">
                  <div className="d-flex justify-content-between align-items-center">
                    <div className="d-flex align-items-center"> <i className="fa fa-long-arrow-left"></i> <a onClick={() => {location("/")}} > <h5 style={{cursor:"pointer"}} >Back</h5></a> </div> <i className="fa fa-shopping-cart text-muted"></i>
                  </div>
                  <div className="mt-4 mb-3"> <span className="text-uppercase text-muted brand">{product.category.name}</span>
                    <h5 className="text-uppercase">{product.title}</h5>
                    <div className="price d-flex flex-row align-items-center"> 
                      <span className="act-price">{product.price} .kr</span>

                    </div>
                  </div>
                  <h6 className="about">{product.description}</h6>
                  <div className="sizes mt-5">
                    
                    {/* <Quantity quantity={currentQuantity} addtocart={handelAddRemoveItem} /> */}
                    {/* <h6 className="text-uppercase">Size</h6> <label className="radio"> <input type="radio" name="size" value="S" checked /> <span>S</span> </label> <label className="radio"> <input type="radio" name="size" value="M" /> <span>M</span> </label> <label className="radio"> <input type="radio" name="size" value="L" /> <span>L</span> </label> <label className="radio"> <input type="radio" name="size" value="XL" /> <span>XL</span> </label> <label className="radio"> <input type="radio" name="size" value="XXL" /> <span>XXL</span> </label> */}
                  </div>
                  <CustomizedButton btntext="Add to cart" btncolor="btn-danger" clicked={handelAddProductToCart}  value={product} />

                  {/* <div className="cart mt-4 align-items-center"> <button className="btn btn-danger text-uppercase mr-2 px-4">Add to cart</button> <i className="fa fa-heart text-muted"></i> <i className="fa fa-share-alt text-muted"></i> </div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

ProductDetails.propTypes = {


  product: PropTypes.object
}

export default ProductDetails