import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BiCart } from "react-icons/bi";
import "./navbarStyle.css"
import { Routes, Route, Link, BrowserRouter } from "react-router-dom";
import MainPage from "./MainPage";
import Categories from "./Categories";
import About from './About'; 
import images from '../SRStorePic/';
import { useSelector } from 'react-redux';
import Cart from './Cart';
import React, {useState} from 'react';
import ProductDetails from './ProductDetails';


function BoostrapNavbar({items}) {

    const cart = useSelector(state => state.cart)


   



    return (
        <BrowserRouter>
            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand as={Link} to="/home"><img
                        src={images.SRLogo}
                        width="50"
                        height="50"
                        className="d-inline-block align-top"
                        // alt="React Bootstrap logo"
                    /></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                            <Navbar.Brand as={Link} to="/home">Home</Navbar.Brand>
                            <Nav.Link as={Link} to="/categories">Categories</Nav.Link>
                            <Nav.Link as={Link} to="/about">About us</Nav.Link>
                    </Nav>
                        <Nav>
                            <Navbar.Text id='mainCart'>
                                {/* Total items: */}
                                
                                <Link to="/cart" id="cartCount">
                                    <BiCart id='carticon' />
                                    <p style={{ visibility: cart.totalQuantity== 0 ? "hidden" : "visible" }}>{cart.totalQuantity}</p>
                                </Link>

                            </Navbar.Text>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <Routes>
                <Route path="/" element={<MainPage />} />

                <Route path="/home" element={<MainPage />} />


                <Route path="/categories" element={<Categories />} />

                <Route path="/about" element={<About />} />

                <Route path="/cart" element={<Cart />} />

                <Route path='/productdetails' element={<ProductDetails/>}/>

    

            </Routes>
        </BrowserRouter>
    );
}

export default BoostrapNavbar;