import React from 'react'
import PropTypes from 'prop-types'
import "./Cart.css"
import { useSelector, useDispatch } from 'react-redux'
import { CgArrowUpR} from "react-icons/cg";
import { add, remove } from '../Store/CartSlices';

const Cart = props => {

const buttonName = {
    plus : "plusIcon",
    minus : "minusIcon"
}

  const cart = useSelector(state => state.cart);
  const dispatch = useDispatch(null);
 
  const handelClick = (args,product) => {

    args.target.className.split(" ")[1] === buttonName.plus ? dispatch(add(product)) : dispatch(remove(product))
  }



  return (

    <div className="fullCard">
            <div className="row">
                <div className="col-md-8 cart">
                    <div className="title">
                        <div className="row">
                            <div className="col"><h4><b>Shopping Cart</b></h4></div>
                            <div className="col cartshowtotalitem text-right text-muted"><h6>Total items: {cart.totalQuantity}</h6></div> 
                          
                        </div>
                    </div>  
 
                    {
                      cart.length == 0 ? <h4 className='text-center'>You have no item in the basket</h4>:  
                      cart.allProducts.map((item , index)=> {
                        return(
                          <div key={index} className="row border-top border-bottom">
                          <div className="row main align-items-center">
                              <div className="col-2"><img className="img-fluid" src={item.product.images}/></div>
                              <div className="col">
                                  <div className="row text-muted">{item.product.title}</div>
                                  <div className="row">{`${item.product.description.substring(0, 25)}...`}</div>
                              </div>
                              <div className="col">
                                  <a href="#" className={`cartRemoveAddIcon ${buttonName.minus}`} onClick={(args) => handelClick(args,item.product)} >-</a><a href="#" className="cartContentborder ">{item.quantity}</a><a href="#" onClick={(args) => handelClick(args,item.product)} className={`cartRemoveAddIcon ${buttonName.plus}`}>+</a>
                              </div>
                              <div className="col">&euro; 44.00 <span className="close">&#10005;</span></div>
                          </div>
                      </div>
                        )






                      })
                    }  

                   
                    <div className="back-to-shop d-flex justify-content-center col align-items-center"><CgArrowUpR/><span> Back to shop</span><CgArrowUpR/></div>
                </div>
                <div className="col-md-4 summary">
                    <div><h5><b>Summary</b></h5></div>
                    <hr/>
                    <form>
                        <h6>SHIPPING</h6>
                        <select><option className="text-muted">Standard-Delivery- &euro;5.00</option></select>
                        <h6>GIVE CODE</h6>
                        <input id="code" placeholder="Enter your code"/>
                    </form>
                    <div className="row" style={{borderTop: "1px solid rgba(0,0,0,.1)", padding: "2vh 0"}}>
                        <div className="col">TOTAL PRICE</div>
                        <div className="col text-right">&euro; 137.00</div>
                    </div>
                    <button className="btn checkoutBtn">CHECKOUT</button>
                </div>
            </div>
            
        </div>
  )
}

Cart.propTypes = {}

export default Cart
