const { createSlice } = require("@reduxjs/toolkit")


const cartSlice = createSlice({
    name: "cart",
    initialState: {
        allProducts: [],
        totalQuantity: 0,
        currentProductdetails : 0

    },


    reducers: {

        add(state, action) {

            const index = state.allProducts.findIndex(item => item.productID == action.payload.id); //finding index of the item

            if (index !== -1) {

                state.allProducts[index].quantity += 1;
                state.totalQuantity += 1;
            }

            else {
                state.allProducts.push({
                    productID: action.payload.id,
                    quantity: 1,
                    product: action.payload
                })
                state.totalQuantity += 1;
            }
        },

        remove(state, action) {
            const index = state.allProducts.findIndex(item => item.productID == action.payload.id); //finding index of the item
            state.totalQuantity -= 1;
            state.allProducts[index].quantity -= 1

            if (state.allProducts[index].quantity == 0) {
                state.allProducts = state.allProducts.filter(item => item.product.id !== action.payload.id);

            }

        },

        addToProductdetails(state, action) {
                state.currentProductdetails = action.payload;               


        }

    }


})

export const { add, remove, addToProductdetails } = cartSlice.actions
export default cartSlice.reducer;