import React from 'react'
import { useDispatch, useSelector } from 'react-redux';

import "./mainpage.css"
import SlidingImage from "./Caurosel"
import 'bootstrap/dist/css/bootstrap.min.css';
import Products from './Products';
import Filtrering from './Filtrering';
import { add } from '../Store/CartSlices';



// import PropTypes from 'prop-types'

const MainPage = () => {
  const dispatch = useDispatch();
  const Allcategories = useSelector(state => state.CategoriesReducer.data)
  const Allproducts = useSelector(state => state.ProductsReducer.data)

  const handelAddProductToCart = (product)=>{
    dispatch(add(product))
  }

  const [searchBoxText, setSearchBoxText] = React.useState("Show All")
  const [dropDownCategories, setDropDownCategories] = React.useState("Show All")



const searchSetValue = (prop) => {

setSearchBoxText(prop.target.value)
}

const setDropDownValue = (prop) => {

  console.log(prop)
  setDropDownCategories(prop == "" ? "Show All" : prop.name)
}

  return (
    <div id='MainPageRootDiv'>
      <SlidingImage />


      <Filtrering 
      dropdownItems={Allcategories} dropdownText={"Select categories"} 
      btnText={"Search"} btnColor="btn-secondary" 
      searchInPutType="text" searchInputPlaceHolder="Search product"
      searchSetValue = {searchSetValue}
      setDropDownValue = {setDropDownValue} 
      DropDownValue = {dropDownCategories}/>
      


      <Products 
      Items={Allproducts} 
      btntext="Add to cart"
      btncolor="btn-primary"       
      serachValue = {searchBoxText}
      clicked={handelAddProductToCart}
      DropDownValue ={dropDownCategories}
      

      Morebtntest= "More details"
      MorebtnColor = "btn-primary"  
      
      
      />

    </div>
  )
}



export default MainPage