import React from 'react'
import PropTypes from 'prop-types'
import CustomizedButton from "./CustomizedButton"
import {  Card, Button } from 'react-bootstrap';
import { propTypes } from 'react-bootstrap/esm/Image'
import "./products.css"
import { useNavigate} from 'react-router-dom'
import { useSelector,  useDispatch} from 'react-redux';
import { addToProductdetails } from '../Store/CartSlices';



const Products = props => {
  const dispatch = useDispatch(null)

  const navigate = useNavigate();
  const store = useSelector(state => state.cart)

  const handelclick = (value) => {

    
    dispatch(addToProductdetails(value));

    navigate("/productdetails", value)

  }
  return (
    <section className=" pt-5 pb-5 m-4" id='ProductsCardroot'>
    <div className="container">
      <div className="row">

        {
          props.Items.filter(function (currentElement) {

            return props.serachValue == "Show All"? currentElement : currentElement.title.toLowerCase().toString().includes(props.serachValue.toLowerCase())
            
          }).filter(item => {


            return props.DropDownValue == "Show All"? item : item.category.name.toLowerCase() == props.DropDownValue.toLowerCase()
          })
          .map((element, index) => {

            return (
              <div key={index} className="col-lg-4 mb-3 d-flex align-items-stretch prdouctsEachCardRoot" >
                <div className="card">
                  <Card.Img variant="top" src={element.images} />
                  <div className="card-body d-flex flex-column">
                    <h5 className="card-title">{element.title}</h5>
                    <p className="card-text mb-4">{`${element.description.substring(0, 75)}...`}</p>
                    <div className="d-flex justify-content-between"> 
                    <CustomizedButton btntext={props.btntext} btncolor={props.btncolor} clicked={props.clicked}  value={element} />
                    {/* <CustomizedButton btntext={props.Morebtntest} btncolor={props.MorebtnColor} clicked={props.clicked}  value={element} /> */}
                    <Button onClick={() => {handelclick(element)}}>Read More</Button>

                    </div>

                  </div>
                </div>
              </div>



            );
          })



        }


      </div>
    </div>
  </section>
  )
}

Products.propTypes = {

    Items : PropTypes.array,
    btntext: PropTypes.string,
    btncolor: PropTypes.string,
    clicked: PropTypes.func,
    serachValue : PropTypes.string,
    DropDownValue: PropTypes.string,

    Morebtntest: PropTypes.string,
    MorebtnColor: PropTypes.string


}

export default Products