 import React from 'react'
 import PropTypes from 'prop-types'
 import CustomizedButton from "./CustomizedButton"
import { Dropdown } from 'react-bootstrap'
 
 
 const Filtrering = props => {
   return (
  <div className="container mt-5 d-flex justify-content-center flex-nowrap">
    <div className="SearchBox col-sm-8 d-flex justify-content-center flex-nowrap">
      <div className="input-group w-100">
        <input type={props.searchInPutType} onChange={value => props.searchSetValue(value)} className="form-control" placeholder={props.searchInputPlaceHolder} aria-label="Recipient's username" aria-describedby="basic-addon2" />
        <div className="input-group-append d-flex justify-content-center flex-nowrap" style={{ width: "8rem" }} >
         
          <CustomizedButton btntext={props.btnText} btncolor={props.btnColor}/>
        </div>
      </div>
    </div>

    <div className="SelectBox col-sm-2 d-flex justify-content-center flex-nowrap">
    <Dropdown>
        <Dropdown.Toggle variant="success">{props.DropDownValue}</Dropdown.Toggle>
        <Dropdown.Menu>
            <Dropdown.Item onClick={value => props.setDropDownValue("")}>Show All</Dropdown.Item>
          {

            props.dropdownItems.map((element, index) => {
              return(
                <Dropdown.Item key={index} onClick={value => props.setDropDownValue(element)}>
                {element.name}
              </Dropdown.Item>

              );

            })
          }



        </Dropdown.Menu>
      </Dropdown>
    </div>
  </div>
   )
 }
 
 Filtrering.propTypes = {
    dropdownItems : PropTypes.array,
    dropdownText : PropTypes.string,
    setDropDownValue : PropTypes.func,
    DropDownValue : PropTypes.string,

    btnText : PropTypes.string,
    btnColor : PropTypes.string,


    searchInputPlaceHolder: PropTypes.string,
    searchInPutType: PropTypes.string,
    searchSetValue: PropTypes.func,


 }
 
 export default Filtrering