import React from 'react'
import { Carousel } from 'react-bootstrap';
import images from '../SRStorePic/';

const SlidingImage = () => {
    return (
        <div>
            <div>
                <Carousel interval={500} keyboard={false} pauseonhover={true}>
                    <Carousel.Item style={{ 'height': "19vw" }}  >
                        <img style={{ 'height': "19vw" }}
                            className="d-block w-100"
                            src={images.Clothes} />
                        <Carousel.Caption>
                            <h3>Clothes</h3>
                        </Carousel.Caption>
                    </Carousel.Item  >
                    <Carousel.Item style={{ 'height': "19vw" }}>
                        <img style={{ 'height': "19vw" }}
                            className="d-block w-100"
                            src={images.Electronics} />
                        <Carousel.Caption>
                            <h3>Electronics</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item style={{ 'height': "19vw" }}>
                        <img style={{ 'height': "19vw" }}
                            className="d-block w-100"
                            src={images.Furniture} />
                        <Carousel.Caption>
                            <h3>Furniture</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item style={{ 'height': "19vw" }}>
                        <img style={{ 'height': "19vw" }}
                            className="d-block w-100"
                            src={images.Shoes} />
                        <Carousel.Caption>
                            <h3>Shoes</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item style={{ 'height': "19vw" }}>
                        <img style={{ 'height': "19vw" }}
                            className="d-block w-100"
                            src={images.Other} />
                        <Carousel.Caption>
                            <h3>Other Products</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </div>
        </div>
    )
}


export default SlidingImage